import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { AcordoPage } from '../pages/acordo/acordo';
import { PagamentoPage } from '../pages/pagamento/pagamento';
import { AcionamentoPage } from '../pages/acionamento/acionamento';
import { FilaPage } from '../pages/fila/fila';
import { DevedorPage } from '../pages/devedor/devedor';
import { BoletoPage } from '../pages/boleto/boleto';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = HomePage;

  pages: Array<{title: string, component: any, icon: string}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Home', component: HomePage, icon:"ios-home"},
      // { title: 'List', component: ListPage },
      { title: 'Acordos', component: AcordoPage,icon:"ios-thumbs-up" },
      { title: 'Pagamentos', component: PagamentoPage, icon:"logo-usd" },
      { title: 'Acionamentos', component: AcionamentoPage, icon:"ios-paper" },
      { title: 'Filas', component: FilaPage, icon:"md-trending-up" },
      { title: 'Pesquisa Devedor', component: DevedorPage, icon:"ios-contact" },
      { title: "Boletos", component: BoletoPage, icon:"ios-mail"}

    ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



